import React, { useEffect } from 'react';
import { Link } from 'react-router-dom';
import { Button } from '@material-ui/core';
import { Footer, GoodCard, Header } from '../../Components';
import { LoadingScreen } from '../../Components/UI/LoadingScreen';
import {useServerMutation, useServerQuery} from '../../hooks';
import { GetGuitars } from '../../server';
import { ExportToCsv } from 'export-to-csv';

import './Goods.scss'

export const GoodsPage: React.FC = () => {
    const urlParams = new URLSearchParams(window.location.search);
    const typeId = urlParams.get('typeId');
    const page = urlParams.get('page');
    const currentPage = page ? Number(page) : 1;

    const { data, isLoading, mutate } = useServerMutation('gfgasdbd', GetGuitars);
    const { data: allData, isLoading: allLoading } = useServerQuery('allGuitars', GetGuitars, {
        page: 1,
        pageSize: 1000,
        sort: 'Date',
        order: 'DESC',
    });

    const handleGetAllGuitars = () => {
        const options = { 
            fieldSeparator: ', ',
            quoteStrings: '"',
            decimalSeparator: '.',
            showLabels: true, 
            showTitle: true,
            title: 'Гитары',
            useTextFile: false,
            useBom: true,
            headers: ['id', 'cost', 'count', 'dateCreated', 'description', 'guitarTypeId', 'name', 'rating']
          };
        const exporter = new ExportToCsv(options);

        exporter.generateCsv(allData!.items);
    }

    useEffect(() => {
        mutate({
            typeId: typeId ? Number(typeId) : undefined,
            sort: 'Date',
            order: 'ASC',
            page: currentPage
        });
    }, [currentPage]);

    if (isLoading) {
        return <LoadingScreen/>;
    }

    const productsToShow = data?.items?.map((product) => (
        <GoodCard key={product.id as number} id={product.id} name={product.name} cost={product.cost} ImgPath={product.img}/>
    ));

    const pageCount = Math.ceil((data?.totalCount || 0) / 10);
    
    
    const ar = [];

    for (let index = 0; index < pageCount; index++) {
        ar[index] = 0;
    }
    
    const pagesToShow = ar.map((_, index) => {
        const page = index + 1;        

        if (page === currentPage) {
            return <div className="currentPage">{currentPage}</div>;
        }

        return <Link className="pages" to={`/goods?page=${page}${typeId ? '&typeId=' + typeId : '' }`}>{page}</Link>
    });

    return (
        <div className="App">
            <Header/>
            <div>
                {allLoading ? 'Загрузка гитар...' : (
                    <Button onClick={handleGetAllGuitars}>
                        Скачать товарный лист
                    </Button>
                )}
            </div>
            <div className="CardContainer">
                {productsToShow}
            </div>
            <div style={{ display: 'flex', width: '100%', justifyContent: 'center', margin: '10px 0' }}>
                {pagesToShow}
            </div>
            
            <Footer/>
        </div>
    );
}